*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  line-height: 1.6;
  padding: 5rem;
  color: $text-color;
  font-size: 1.6rem;
  font-weight: 300;
}

a:link,
a:visited,
a:active {
  color: inherit;
}

.container {
  box-shadow: 0 0 3rem rgba(black, 0.3);
}